<template>
  <div>
    <nav-bar>
      <template v-slot:default>商品详情</template>
    </nav-bar>

    <van-image
      style="margin-top:50px"
      width="100%"
      lazy-load
      :src="details.cover_url" />

    <van-card style="text-align: left;"
      :num="details.stock"
      :price="details.price+'.00'"
      :desc="details.description"
      :title="details.title"
    >
      <template #tags>
        <van-tag plain type="danger">新书</van-tag>
        <van-tag plain type="danger" v-show="details.is_recommend">推荐</van-tag>
      </template>
      <template #footer>
        <van-button type="warning" @click="handleAddCart">加入购物车</van-button>
        <van-button type="danger" @click="goToCart">立即购买</van-button>
      </template>
    </van-card>

    <van-tabs v-model="active">
      <van-tab title="概述">
        <div class="content" v-html="details.details">

        </div>
      </van-tab>
      <van-tab title="热评">
        <!--<div v-for="item in detail.comments" :key="item.id"></div>-->
      </van-tab>
      <van-tab title="相关图书">
        <goods-list :goods="like_goods"></goods-list>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
  import NavBar from "components/common/navbar/NavBar";
  import GoodsList from "components/content/goods/GoodsList";
  import {useRoute,useRouter} from 'vue-router';
  import {useStore} from 'vuex'
  import {ref, reactive, onMounted, toRefs} from 'vue'
  import {getDetail} from "network/detail";
  import {addCart} from "network/cart";
  import {Toast} from 'vant'


  export default {
    name: "Detail",
    components: {
      NavBar,
      GoodsList
    },
    setup(){
      const route = useRoute();
      const router = useRouter();
      const store = useStore();

      let id = ref(0);
      let active = ref(0);
      let book = reactive({
        details: {},
        like_goods: {},
        comments: {}
      })

      onMounted(()=>{
        id.value = route.query.id;

        getDetail(id.value).then(res=>{
          console.log(res);
           book.details = res.goods;
           book.like_goods = res.like_goods
        })
      })

      //添加购物车
      const handleAddCart =()=>{
        addCart({goods_id:book.details.id,num:1}).then(res=>{
          if(res.status == '201' || res.status == '204'){
            Toast.success('添加成功');
            //设置store中cartCount
            store.dispatch('updateCart')
          }
        })
      }

      //立即购买
      const goToCart = ()=> {
        addCart({goods_id: book.details.id, num: 1}).then(res => {
          if (res.status == '201' || res.status == '204') {
            Toast.success('添加成功，显示购物车');
            store.dispatch('updateCart');
            router.push({path: '/shopcart'});
          }
        })
      }

      return {
        id,
        active,
        ...toRefs(book),
        handleAddCart,
        goToCart
      }
    }
  }
</script>

<style lang="scss">
  .content {
    padding: 10px;
    img {
      width: 100%;
      height: auto;
    }
  }
</style>